async function determineSigns ( birthday ) {
  var user_signs = {'Aries': {'Planet': '',
                              'Decan': 0},
                    'Taurus': '',
                    'Gemini': '',
                    'Cancer': '',
                    'Leo': '',
                    'Virgo': '',
                    'Libra': '',
                    'Scorpio': '',
                    'Sagittarius': '',
                    'Capricorn': '',
                    'Aquarius': '',
                    'Pisces': ''};
  for (const [sign, date_range] of Object.entries(signs)) {
    //Sun
    if (birthday.Month === date_range.Start.Month || birthday.Month === date_range.End.Month) {
      if (birthday.Month === date_range.Start.Month) {
        if (birthday.Day >= date_range.Start.Day) {
          user_signs[sign] = 'Sun';
          //console.log(signString, user_signs.signString);
        }
      } else if (birthday.Month === date_range.End.Month) {
        if (birthday.Day <= date_range.End.Day) {
          user_signs[sign] = 'Sun';
          //console.log(signString, user_signs.signString);
        }
      }
    }
  };
  return user_signs
}



//////////
// Origin
//////////
// This class automatically derives the local timezone from latitude/longitude coordinates
// and calculates UTC time with respect to timezone and historical daylight savings time.
// Only works for C.E. date (> 0).
/////////
// * int year: value >= 0 C.E.
// * int month: (0 = january ...11 = december)
// * int date: (1...31)
// * int hours = local time - hours value (0...23)
// * int minute = local time - minute value (0...59)
// * float latitude = latitude in decimal format (-90.00...90.00)
// * float longitude = longitude in decimal format (-180.00...180.00)

// December 1st, 2020 - 430pm
const origin = new Origin({
  year: 1991,
  month: 5, // 0 = January, 11 = December!
  date: 23,
  hour: 16,
  minute: 30,
  latitude: 40.0,
  longitude: -70.0,
});

